import React from "react";
import { useSelector, useDispatch } from "../../utils/hooks";
import {
  BrowserRouter as Router,
  Switch,
  Route,
  useLocation,
  useHistory,
} from "react-router-dom";

import { getLocalStorageAuth } from "../../services/user";
import { restoreUser } from "../../services/actions/auth";

import AppHeader from "../app-header/app-header";
import IngredientDetails from "../ingredient-details/ingredient-details";
import TicketDetails from "../ticket-details/ticket-details";
import ProtectedRoute from "../protected-route/protected-route";

import ErrorNotifier from "../error-notifier/error-notifier";
import { clearError } from "../../services/actions/error";

import Modal from "../modal/modal";

import {
  MainPage,
  LoginPage,
  RegisterPage,
  ForgotPasswordPage,
  ResetPasswordPage,
  ProfilePage,
  IngredientPreviewPage,
  FeedPage,
  TicketDetailsPage,
  NotFoundPage,
} from "../../pages";

import css from "./app.module.css";
import { getIngredients } from "../../services/actions/ingredient-list";
import mainMenu from "../../utils/menu";

import { TCustomHystory } from "../../utils/types";

// ================================
//      
//   
const RoutedContent = () => {
  //    
  const history = useHistory<TCustomHystory>();
  //   location    
  const location = useLocation<any>();
  const background = location.state && location.state.background;

  const closeModalPreview = () => {
    history.goBack();
  };

  return (
    <>
      <AppHeader menu={mainMenu} />
      <div className={css.contents}>
        <Switch location={background || location}>
          <Route path="/" exact={true}>
            <MainPage />
          </Route>
          <Route path="/login" exact={true}>
            <LoginPage />
          </Route>
          <Route path="/register" exact={true}>
            <RegisterPage />
          </Route>
          <Route path="/forgot-password" exact={true}>
            <ForgotPasswordPage />
          </Route>
          <Route path="/reset-password" exact={true}>
            <ResetPasswordPage />
          </Route>
          {/*     */}
          <ProtectedRoute path="/profile" exact={true}>
            <ProfilePage />
          </ProtectedRoute>
          {/*      */}
          <ProtectedRoute path="/profile/orders" exact={true}>
            <ProfilePage />
          </ProtectedRoute>
          <ProtectedRoute path="/profile/orders/:id" exact={true}>
            <TicketDetailsPage />
          </ProtectedRoute>
          {/*     (  ) */}
          <ProtectedRoute path="/logout" exact={true}>
            <ProfilePage />
          </ProtectedRoute>
          <Route path="/ingredients/:id" exact={true}>
            <IngredientPreviewPage />
          </Route>
          <Route path="/feed" exact={true}>
            <FeedPage />
          </Route>
          <Route path="/feed/:id" exact={true}>
            <TicketDetailsPage />
          </Route>
          <Route>
            <NotFoundPage />
          </Route>
        </Switch>
      </div>
      {background && (
        <>
          <Route
            exact={true}
            path="/ingredients/:id"
            children={
              <Modal
                closeCallback={closeModalPreview}
                header={" "}
              >
                <IngredientDetails />
              </Modal>
            }
          />
          <Route
            exact={true}
            path="/feed/:id"
            children={
              <Modal closeCallback={closeModalPreview} header={" "}>
                <TicketDetails />
              </Modal>
            }
          />
          <ProtectedRoute
            exact={true}
            path="/profile/orders/:id"
            children={
              <Modal closeCallback={closeModalPreview} header={" "}>
                <TicketDetails />
              </Modal>
            }
          />
        </>
      )}
    </>
  );
};

// APP component
const App = () => {
  //     
  const dispatch = useDispatch();

  //    local storage
  const userData = getLocalStorageAuth();

  React.useEffect(() => {
    if (userData != null) {
      dispatch(restoreUser(userData));
    }
  }, [dispatch, userData]);

  //   
  const { ingredients, ingredientRequest } = useSelector((store) => store.list);

  //         
  React.useEffect(() => {
    if (!ingredients.length && !ingredientRequest) {
      dispatch(getIngredients());
    }
  }, [dispatch, ingredients, ingredientRequest]);

  //      
  const { source, message } = useSelector((store) => store.error);

  //          
  const closeErrorModal = () => {
    dispatch(clearError());
  };

  return (
    <div className={css.page}>
      <main className={css.main + " mt-10 ml-10 mr-10"}>
        <Router>
          <RoutedContent />
        </Router>
        {/*      */}
        {message && (
          <Modal closeCallback={closeErrorModal} header={"!"}>
            <ErrorNotifier source={source} message={message} />
          </Modal>
        )}
      </main>
    </div>
  );
};

export default App;
